.FindMatch {
    padding: 50px;

    > .find-match-form {
        > .words {
            list-style: decimal;
            margin-bottom: var(--spacing-full);

            span {
                padding-left: 7px;
            }
        }
    
        > .matches {
            list-style: upper-latin;
            margin-bottom: var(--spacing-full);

            > li {
                margin-bottom: var(--spacing-half);
            }

            span {
                padding-left: 7px;
            }
        }
    }
}

@media screen and (min-width: 640px) {
    .find-match-form {
        display: flex;
        column-gap: var(--spacing-full);
        justify-content: space-between;
        flex-wrap: wrap;
    }
    
    .buttons-wrap {
        margin: auto;
    }
}